// src/supabaseClient.js
import { createClient } from '@supabase/supabase-js'

//    ( Create React App  process.env)
//    ,    backend API
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

//   ( ,    backend)
let supabaseUrl = process.env.REACT_APP_SUPABASE_URL || 'https://mzmouzcbmyhktvowrztm.supabase.co'
let supabaseKey = process.env.REACT_APP_SUPABASE_ANON_KEY || ''

//         
function getClientOptions(supabaseKey) {
  const options = {
    auth: {
      autoRefreshToken: false, //  ,     Supabase Auth
      persistSession: true,
      detectSessionInUrl: false,
      storage: typeof window !== 'undefined' ? window.localStorage : undefined,
    },
    db: {
      schema: 'public',
    },
  };

  //  apikey  ,   
  if (supabaseKey) {
    options.global = {
      headers: {
        'apikey': supabaseKey,
      },
    };
  }

  return options;
}

//   Supabase Auth   localStorage  
if (typeof window !== 'undefined') {
  try {
    const keys = Object.keys(localStorage);
    keys.forEach(key => {
      if (key.includes('supabase') && (key.includes('auth') || key.includes('token'))) {
        localStorage.removeItem(key);
      }
    });
  } catch (e) {
    //    
  }
}

//  
let supabase = null;

// ,  URL    
if (supabaseUrl && supabaseKey && supabaseKey.trim() !== '') {
  try {
    supabase = createClient(supabaseUrl, supabaseKey, getClientOptions(supabaseKey));
    console.log(' Supabase     ');
  } catch (error) {
    console.error('   Supabase :', error);
    supabase = null;
  }
}

//      ,    backend (  )
if ((!supabaseKey || supabaseKey.trim() === '') && typeof window !== 'undefined') {
  fetch(`${API_URL}/api/config`)
    .then(response => {
      if (response.ok) {
        return response.json();
      }
      throw new Error('   ');
    })
    .then(config => {
      if (config && config.supabaseAnonKey && config.supabaseAnonKey.trim() !== '') {
        supabaseUrl = config.supabaseUrl || supabaseUrl;
        supabaseKey = config.supabaseAnonKey;
        
        //     
        try {
          supabase = createClient(supabaseUrl, supabaseKey, getClientOptions(supabaseKey));
          console.log(' Supabase      backend');
        } catch (error) {
          console.error('   Supabase :', error);
          if (!supabase) {
            createFallbackClient();
          }
        }
      } else {
        if (!supabase) {
          createFallbackClient();
        }
      }
    })
    .catch(error => {
      console.warn('       backend:', error.message);
      console.warn(' ,  backend    SUPABASE_ANON_KEY  .env');
      if (!supabase) {
        createFallbackClient();
      }
    });
}

//  ,    
function createFallbackClient() {
  if (!supabase) {
    console.warn('  Supabase   :  URL  ');
    
    //     
    const createQueryBuilder = () => {
      const query = {
        select: (columns = '*') => ({
          ...query,
          order: (column, options = {}) => Promise.resolve({ data: [], error: null }),
          eq: (column, value) => ({
            ...query,
            single: () => Promise.resolve({ data: null, error: { message: 'Supabase  ' } }),
          }),
          ilike: (column, pattern) => ({
            ...query,
            limit: (count) => Promise.resolve({ data: [], error: null }),
          }),
          or: (pattern) => ({
            ...query,
            limit: (count) => Promise.resolve({ data: [], error: null }),
          }),
        }),
        insert: () => Promise.resolve({ data: null, error: { message: 'Supabase  ' } }),
        update: () => Promise.resolve({ data: null, error: { message: 'Supabase  ' } }),
        delete: () => Promise.resolve({ data: null, error: { message: 'Supabase  ' } }),
      };
      return query;
    };
    
    supabase = {
      from: (table) => createQueryBuilder(),
      channel: (name) => ({
        on: () => ({
          subscribe: () => ({
            unsubscribe: () => {},
          }),
        }),
      }),
      auth: { 
        signOut: async () => {},
        getSession: async () => ({ data: { session: null } }),
      },
      storage: { 
        from: () => ({ 
          upload: async () => ({ data: null, error: { message: 'Supabase  ' } }),
          getPublicUrl: () => ({ publicUrl: '' }),
        }) 
      },
    };
  }
}

//      ,  
if (!supabase) {
  createFallbackClient();
}

//      
export const auth = supabase.auth
export const db = supabase
export const storage = supabase.storage

export { supabase }
