//     WhatsApp
// : alert   ,         
//  ,   ,    
export const openWhatsApp = (message = '') => {
  const defaultMessage = '!       Sezim.abu.';
  const finalMessage = message || defaultMessage;
  
  console.log(' WhatsApp  :', finalMessage);
  
  try {
    const whatsappUrl = `https://wa.me/?text=${encodeURIComponent(finalMessage)}`;
    console.log('WhatsApp URL:', whatsappUrl);
    window.open(whatsappUrl, '_blank');
  } catch (error) {
    console.error('   WhatsApp:', error);
    //      
    throw error;
  }
};

//      
export const openWhatsAppForPsychologist = (psychologistName, language = 'ru') => {
  console.log('openWhatsAppForPsychologist :', { psychologistName, language });
  
  const messages = {
    ru: `!      ${psychologistName}.`,
    kz: ` ! ${psychologistName}    .`,
  };
  
  const message = messages[language] || messages.ru;
  console.log(' :', message);
  openWhatsApp(message);
};

//    
export const openWhatsAppForGeneralAppointment = (language = 'ru') => {
  const messages = {
    ru: '!          Sezim.abu.',
    kz: ' ! Sezim.abu       .',
  };
  
  const message = messages[language] || messages.ru;
  openWhatsApp(message);
};
