//       API
//  YOUR_DOMAIN   

const API_URL = process.env.REACT_APP_API_URL || window.location.origin;

//      Render API
export const sendContactMessage = async (formData) => {
  try {
    console.log('   Render API...');
    
    const response = await fetch(`${API_URL}/api/send`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        email: formData.email,
        subject: formData.subject,
        message: formData.message,
        name: formData.name || ' ',
        phone: formData.phone || ' ',
      }),
    });

    // ,      JSON
    if (!response.ok) {
      const errorText = await response.text().catch(() => 'Unknown error');
      throw new Error(`Server error: ${response.status} ${errorText}`);
    }

    // ,     
    const contentType = response.headers.get('content-type');
    if (!contentType || !contentType.includes('application/json')) {
      const text = await response.text();
      throw new Error(`Expected JSON but got: ${contentType}. Response: ${text.substring(0, 100)}`);
    }

    const result = await response.json();

    return {
      ok: true,
      message: '  !',
      response: result
    };

  } catch (error) {
    console.error('    Render API:', error);
    
    // ,     (  )
    if (error.name === 'TypeError' && (error.message.includes('fetch') || error.message.includes('Failed to fetch'))) {
      return {
        ok: false,
        message: `    .  REACT_APP_API_URL (: ${API_URL})`,
        error: error
      };
    }
    
    //    JSON
    if (error.message.includes('JSON') || error.message.includes('Unexpected end')) {
      return {
        ok: false,
        message: `   . ,  backend     : ${API_URL}`,
        error: error
      };
    }
    
    return {
      ok: false,
      message: error.message || '   .   .',
      error: error
    };
  }
};

//     API
export const checkApiHealth = async () => {
  try {
    const response = await fetch(`${API_URL}/api/health`);
    const result = await response.json();
    
    return {
      ok: response.ok,
      status: result.status,
      timestamp: result.timestamp
    };
  } catch (error) {
    console.error('   API:', error);
    return {
      ok: false,
      error: error.message
    };
  }
};

//      
export const subscribeToNews = async (email) => {
  try {
    const response = await fetch(`${API_URL}/api/subscribe`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        email: email,
        source: 'website'
      }),
    });

    const result = await response.json();

    if (response.ok) {
      return {
        ok: true,
        message: '  !',
        response: result
      };
    } else {
      throw new Error(result.error || ' ');
    }

  } catch (error) {
    console.error('  :', error);
    return {
      ok: false,
      message: error.message || '   .   .',
      error: error
    };
  }
};
