import React, { useEffect, useState } from "react";
import { supabase } from "../../supabaseClient";
import "./PsychologistsAdmin.css";

export default function AlphabetAdmin() {
  const [terms, setTerms] = useState([]);
  const [loading, setLoading] = useState(true);
  const [addingTerm, setAddingTerm] = useState(false);
  const [editingId, setEditingId] = useState(null);
  const [searchQuery, setSearchQuery] = useState("");

  //  
  const [letter, setLetter] = useState("");
  const [termRu, setTermRu] = useState("");
  const [termKz, setTermKz] = useState("");
  const [image, setImage] = useState("");
  const [definitionRu, setDefinitionRu] = useState("");
  const [definitionKz, setDefinitionKz] = useState("");
  const [fullContentJson, setFullContentJson] = useState("{}");
  const [orderIndex, setOrderIndex] = useState(0);
  const [imageMode, setImageMode] = useState("url"); // "url"  "upload"
  const [imageUrl, setImageUrl] = useState("");
  const [imageFile, setImageFile] = useState(null);
  const [uploadedImageUrl, setUploadedImageUrl] = useState("");
  const [uploadProgress, setUploadProgress] = useState(0);

  useEffect(() => {
    loadTerms();
  }, []);

  const loadTerms = async () => {
    try {
      setLoading(true);
      console.log('   Supabase...');
      const { data, error } = await supabase
        .from('alphabet_items')
        .select('*')
        .order('letter', { ascending: true })
        .order('order_index', { ascending: true });

      if (error) {
        console.error(' Supabase:', error);
        throw error;
      }

      console.log(' :', data?.length || 0);
      
      //      
      const sortedData = (data || []).sort((a, b) => {
        //   
        if (a.letter !== b.letter) {
          return a.letter.localeCompare(b.letter);
        }
        
        //   order_index (    > 0)
        const orderA = a.order_index || 0;
        const orderB = b.order_index || 0;
        
        //    order_index > 0,   
        if (orderA > 0 && orderB > 0) {
          return orderA - orderB;
        }
        
        //     order_index > 0,   
        if (orderA > 0 && orderB === 0) return -1;
        if (orderA === 0 && orderB > 0) return 1;
        
        //    order_index = 0  ,    
        const termA = (a.term_ru || '').toLowerCase();
        const termB = (b.term_ru || '').toLowerCase();
        return termA.localeCompare(termB);
      });
      
      setTerms(sortedData);
    } catch (error) {
      console.error('  :', error);
      alert("  : " + error.message);
    } finally {
      setLoading(false);
    }
  };

  //      
  const getSafeFileName = (originalName) => {
    const extension = originalName.substring(originalName.lastIndexOf('.'));
    const nameWithoutExt = originalName.substring(0, originalName.lastIndexOf('.'));
    
    const transliterate = (str) => {
      const ru = '';
      const en = 'abvgdeezziyklmnoprstufhccss_y_eua';
      
      return str.toLowerCase().split('').map(char => {
        const index = ru.indexOf(char);
        return index !== -1 ? en[index] : char;
      }).join('');
    };
    
    const safeName = transliterate(nameWithoutExt)
      .replace(/[^\w.-]/g, '_')
      .replace(/_+/g, '_')
      .replace(/^_|_$/g, '')
      .toLowerCase();
    
    const finalName = safeName || 'alphabet';
    return `${finalName}${extension}`;
  };

  //    Supabase Storage
  const uploadImage = async (file) => {
    if (!file) {
      throw new Error("  ");
    }

    if (!file.type.startsWith('image/')) {
      throw new Error(",  ");
    }

    try {
      setUploadProgress(10);
      const timestamp = Date.now();
      const safeFileName = getSafeFileName(file.name);
      const fileName = `alphabet/${timestamp}_${safeFileName}`;

      setUploadProgress(30);
      
      //    bucket,    ,    alphabet
      //  upsert: true,      
      const { data: uploadData, error: uploadError } = await supabase.storage
        .from('news-images')
        .upload(fileName, file, {
          cacheControl: '3600',
          upsert: true
        });

      if (uploadError) {
        console.error("   Storage:", uploadError);
        //    ,    URL
        if (uploadError.message && uploadError.message.includes('already exists')) {
          console.log("  ,  URL...");
        } else {
          throw uploadError;
        }
      }

      setUploadProgress(70);

      //   URL
      const pathToUse = uploadData?.path || fileName;
      console.log("  :", pathToUse);
      
      const { data: publicUrlData } = supabase.storage
        .from('news-images')
        .getPublicUrl(pathToUse);

      setUploadProgress(90);
      
      let finalUrl = publicUrlData?.publicUrl;
      
      //    URL  getPublicUrl,  
      if (!finalUrl) {
        const supabaseUrl = process.env.REACT_APP_SUPABASE_URL || 'https://mzmouzcbmyhktvowrztm.supabase.co';
        finalUrl = `${supabaseUrl}/storage/v1/object/public/news-images/${pathToUse}`;
        console.log("  URL:", finalUrl);
      }

      console.log("  ,  URL:", finalUrl);
      
      // ,  URL 
      if (!finalUrl || !finalUrl.startsWith('http')) {
        throw new Error("    URL  ");
      }
      
      setUploadProgress(100);
      setTimeout(() => setUploadProgress(0), 500);
      return finalUrl;
    } catch (error) {
      console.error(" :", error);
      setUploadProgress(0);
      throw error;
    }
  };

  const resetForm = () => {
    setEditingId(null);
    setLetter("");
    setTermRu("");
    setTermKz("");
    setImage("");
    setDefinitionRu("");
    setDefinitionKz("");
    setFullContentJson("{}");
    setOrderIndex(0);
    setImageUrl("");
    setImageFile(null);
    setUploadedImageUrl("");
    setImageMode("url");
    setUploadProgress(0);
  };

  const editTerm = (term) => {
    setEditingId(term.id);
    setLetter(term.letter || "");
    setTermRu(term.term_ru || "");
    setTermKz(term.term_kz || "");
    setImage(term.image || "");
    setDefinitionRu(term.definition_ru || "");
    setDefinitionKz(term.definition_kz || "");
    setFullContentJson(term.full_content ? JSON.stringify(term.full_content, null, 2) : "{}");
    setOrderIndex(term.order_index || 0);
    setImageUrl(term.image || "");
    setImageFile(null);
    setUploadedImageUrl(term.image || "");
    setImageMode(term.image && term.image.startsWith('http') ? "url" : "upload");
    setUploadProgress(0);
    
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const saveTerm = async (e) => {
    e.preventDefault();
    
    if (!termRu || !termKz || !letter) {
      return alert("      ");
    }

    setAddingTerm(true);
    try {
      //  URL     
      let finalImageUrl = "";
      if (imageMode === "upload" && imageFile) {
        //    Supabase Storage
        console.log(" ...");
        finalImageUrl = await uploadImage(imageFile);
        console.log(" , URL:", finalImageUrl);
      } else if (imageMode === "url" && imageUrl.trim()) {
        //   URL
        finalImageUrl = imageUrl.trim();
      } else if (imageMode === "url" && image.trim()) {
        //    image (  )
        finalImageUrl = image.trim();
      } else if (uploadedImageUrl && uploadedImageUrl.startsWith('http')) {
        //      ( )
        finalImageUrl = uploadedImageUrl;
      }

      //  JSON  fullContent
      let fullContent = {};
      try {
        fullContent = JSON.parse(fullContentJson);
      } catch (e) {
        console.warn("  fullContent,    ");
        fullContent = {};
      }

      const termData = {
        letter: letter.toUpperCase(),
        term_ru: termRu.trim(),
        term_kz: termKz.trim(),
        image: finalImageUrl || null,
        definition_ru: definitionRu.trim() || null,
        definition_kz: definitionKz.trim() || null,
        full_content: fullContent,
        order_index: orderIndex || 0,
      };

      console.log("   :", termData);
      console.log("URL :", termData.image);
      
      let error;
      if (editingId) {
        const { error: updateError } = await supabase
          .from('alphabet_items')
          .update(termData)
          .eq('id', editingId);
        error = updateError;
      } else {
        const { error: insertError } = await supabase
          .from('alphabet_items')
          .insert(termData);
        error = insertError;
      }

      if (error) {
        console.error("   :", error);
        throw error;
      }

      console.log("  !");
      resetForm();
      loadTerms();
      alert(editingId ? " !" : " !");
    } catch (error) {
      alert("  : " + error.message);
      console.error(error);
    } finally {
      setAddingTerm(false);
    }
  };

  const deleteTerm = async (id) => {
    if (!window.confirm(" ,     ?")) {
      return;
    }

    try {
      const { error } = await supabase
        .from('alphabet_items')
        .delete()
        .eq('id', id);

      if (error) {
        throw error;
      }

      loadTerms();
      alert(" !");
    } catch (error) {
      alert("  : " + error.message);
      console.error(error);
    }
  };

  //     
  const filteredTerms = terms.filter(term => {
    if (!searchQuery.trim()) return true;
    const query = searchQuery.toLowerCase();
    return (
      term.term_ru?.toLowerCase().includes(query) ||
      term.term_kz?.toLowerCase().includes(query) ||
      term.letter?.toLowerCase().includes(query) ||
      term.definition_ru?.toLowerCase().includes(query) ||
      term.definition_kz?.toLowerCase().includes(query)
    );
  });

  if (loading) {
    return (
      <div style={{ textAlign: 'center', padding: '2rem' }}>
        <p> ...</p>
      </div>
    );
  }

  return (
    <div>
      <section className="admin-section">
        <h2 className="section-title">
          {editingId ? "  " : "  "}
        </h2>
        <form onSubmit={saveTerm}>
          <div style={{ display: 'grid', gridTemplateColumns: '100px 1fr 1fr', gap: '20px', marginBottom: '20px' }}>
            <div className="form-group">
              <label htmlFor="letter"> *</label>
              <input
                id="letter"
                type="text"
                value={letter}
                onChange={e => setLetter(e.target.value.toUpperCase())}
                placeholder=""
                maxLength="1"
                required
                style={{ textTransform: 'uppercase' }}
              />
            </div>

            <div className="form-group">
              <label htmlFor="term-ru"> () *</label>
              <input
                id="term-ru"
                type="text"
                value={termRu}
                onChange={e => setTermRu(e.target.value)}
                placeholder=" "
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="term-kz"> () *</label>
              <input
                id="term-kz"
                type="text"
                value={termKz}
                onChange={e => setTermKz(e.target.value)}
                placeholder=" "
                required
              />
            </div>
          </div>

          <div className="form-group" style={{ marginBottom: '20px' }}>
            <label htmlFor="image-mode"> </label>
            <div style={{ display: 'flex', gap: '10px', marginBottom: '10px' }}>
              <button
                type="button"
                onClick={() => setImageMode("url")}
                style={{
                  padding: '8px 16px',
                  background: imageMode === "url" ? '#667eea' : '#f0f0f0',
                  color: imageMode === "url" ? 'white' : '#333',
                  border: 'none',
                  borderRadius: '8px',
                  cursor: 'pointer',
                  transition: 'all 0.3s'
                }}
              >
                 URL
              </button>
              <button
                type="button"
                onClick={() => setImageMode("upload")}
                style={{
                  padding: '8px 16px',
                  background: imageMode === "upload" ? '#667eea' : '#f0f0f0',
                  color: imageMode === "upload" ? 'white' : '#333',
                  border: 'none',
                  borderRadius: '8px',
                  cursor: 'pointer',
                  transition: 'all 0.3s'
                }}
              >
                  
              </button>
            </div>

            {/*  URL */}
            {imageMode === "url" && (
              <>
                <input
                  id="image-url"
                  type="url"
                  value={imageUrl}
                  onChange={e => setImageUrl(e.target.value)}
                  placeholder="https://example.com/image.jpg  /images/image.jpg"
                  style={{
                    width: '100%',
                    padding: '12px',
                    border: '1px solid #ddd',
                    borderRadius: '8px',
                    fontSize: '14px'
                  }}
                />
                <p className="field-hint" style={{ marginTop: '8px', fontSize: '13px', color: '#666' }}>
                      <a href="https://imgur.com/upload" target="_blank" rel="noopener noreferrer">Imgur</a>     
                </p>
              </>
            )}

            {/*    */}
            {imageMode === "upload" && (
              <>
                <input
                  id="image-file"
                  type="file"
                  accept="image/*"
                  onChange={(e) => {
                    const file = e.target.files[0];
                    setImageFile(file);
                    if (file) {
                      //  
                      const reader = new FileReader();
                      reader.onloadend = () => {
                        setUploadedImageUrl(reader.result);
                      };
                      reader.readAsDataURL(file);
                    }
                  }}
                  style={{
                    width: '100%',
                    padding: '12px',
                    border: '2px dashed #667eea',
                    borderRadius: '8px',
                    fontSize: '14px',
                    background: '#f8f9ff'
                  }}
                />
                <p className="field-hint" style={{ marginTop: '8px', fontSize: '13px', color: '#666' }}>
                        (JPG, PNG, GIF).     Supabase Storage.
                </p>

                {/*    */}
                {uploadProgress > 0 && uploadProgress < 100 && (
                  <div style={{ marginTop: '10px' }}>
                    <div style={{
                      width: '100%',
                      height: '8px',
                      backgroundColor: '#e0e0e0',
                      borderRadius: '4px',
                      overflow: 'hidden'
                    }}>
                      <div style={{
                        width: `${uploadProgress}%`,
                        height: '100%',
                        backgroundColor: '#667eea',
                        transition: 'width 0.3s ease',
                        borderRadius: '4px'
                      }}></div>
                    </div>
                    <p style={{ marginTop: '5px', fontSize: '12px', color: '#667eea', textAlign: 'center' }}>
                      : {uploadProgress}%
                    </p>
                  </div>
                )}

                {/*   */}
                {uploadedImageUrl && (
                  <div style={{ marginTop: '15px' }}>
                    <img
                      src={uploadedImageUrl}
                      alt=""
                      style={{
                        maxWidth: '300px',
                        maxHeight: '200px',
                        borderRadius: '8px',
                        border: '1px solid #ddd',
                        objectFit: 'contain'
                      }}
                      onError={(e) => {
                        console.error("  :", uploadedImageUrl);
                        e.target.style.display = 'none';
                      }}
                    />
                    {uploadedImageUrl.startsWith('http') && (
                      <p style={{ marginTop: '5px', fontSize: '12px', color: '#4caf50' }}>
                            
                      </p>
                    )}
                  </div>
                )}
              </>
            )}
          </div>

          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', marginBottom: '20px' }}>
            <div className="form-group">
              <label htmlFor="definition-ru"> ()</label>
              <textarea
                id="definition-ru"
                value={definitionRu}
                onChange={e => setDefinitionRu(e.target.value)}
                placeholder=",   ..."
                rows="3"
              />
            </div>

            <div className="form-group">
              <label htmlFor="definition-kz"> ()</label>
              <textarea
                id="definition-kz"
                value={definitionKz}
                onChange={e => setDefinitionKz(e.target.value)}
                placeholder="     ..."
                rows="3"
              />
            </div>
          </div>

          <div className="form-group" style={{ marginBottom: '20px' }}>
            <label htmlFor="full-content">  (JSON)</label>
            <textarea
              id="full-content"
              value={fullContentJson}
              onChange={e => setFullContentJson(e.target.value)}
              placeholder='{"ru": {"description": "...", "example": "...", "explanation": "...", "symptoms": [...], "advice": [...]}, "kz": {...}}'
              rows="15"
              style={{ fontFamily: 'monospace', fontSize: '12px' }}
            />
            <p className="field-hint">
                   JSON.     {}   .
            </p>
          </div>

          <div className="form-group" style={{ marginBottom: '20px' }}>
            <label htmlFor="order-index"> </label>
            <input
              id="order-index"
              type="number"
              value={orderIndex}
              onChange={e => setOrderIndex(parseInt(e.target.value) || 0)}
              min="0"
              placeholder="0"
              style={{
                width: '100%',
                padding: '12px',
                border: '1px solid #ddd',
                borderRadius: '8px',
                fontSize: '14px'
              }}
            />
            <div style={{
              marginTop: '8px',
              padding: '12px',
              backgroundColor: '#f0f7ff',
              border: '1px solid #b3d9ff',
              borderRadius: '8px',
              fontSize: '13px',
              lineHeight: '1.6'
            }}>
              <strong>   :</strong>
              <ul style={{ margin: '8px 0 0 0', paddingLeft: '20px' }}>
                <li><strong>0</strong> =   (  )</li>
                <li><strong>1, 2, 3...</strong> =   (  =   )</li>
                <li>        0</li>
              </ul>
              <p style={{ margin: '8px 0 0 0', fontSize: '12px', color: '#666' }}>
                <strong>:</strong>    ""     1, 5, 10, 0, 0 - 
                  : 1  5  10  (  )
              </p>
            </div>
          </div>

          <button
            type="submit"
            className="btn btn-primary"
            disabled={addingTerm}
          >
            {addingTerm
              ? (editingId ? "..." : "...")
              : (editingId ? "  " : "  ")}
          </button>
          {editingId && (
            <button
              type="button"
              onClick={resetForm}
              className="btn-secondary"
              style={{ marginLeft: '10px' }}
            >
              
            </button>
          )}
        </form>
      </section>

      <section className="admin-section">
        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '20px', flexWrap: 'wrap', gap: '15px' }}>
          <h2 className="section-title" style={{ margin: 0 }}>
              ({filteredTerms.length} {searchQuery ? ` ${terms.length}` : ''})
          </h2>
          <div style={{ position: 'relative', minWidth: '300px', flex: '1', maxWidth: '500px' }}>
            <input
              type="text"
              placeholder="   , , ..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              style={{
                width: '100%',
                padding: '12px 40px 12px 15px',
                border: '2px solid #ddd',
                borderRadius: '10px',
                fontSize: '14px',
                transition: 'all 0.3s ease',
                boxSizing: 'border-box'
              }}
              onFocus={(e) => {
                e.target.style.borderColor = '#667eea';
                e.target.style.boxShadow = '0 0 0 3px rgba(102, 126, 234, 0.1)';
              }}
              onBlur={(e) => {
                e.target.style.borderColor = '#ddd';
                e.target.style.boxShadow = 'none';
              }}
            />
            {searchQuery && (
              <button
                onClick={() => setSearchQuery("")}
                style={{
                  position: 'absolute',
                  right: '10px',
                  top: '50%',
                  transform: 'translateY(-50%)',
                  background: 'transparent',
                  border: 'none',
                  cursor: 'pointer',
                  fontSize: '18px',
                  color: '#999',
                  padding: '5px',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center'
                }}
                title=" "
              >
                
              </button>
            )}
          </div>
        </div>

        {terms.length === 0 ? (
          <div className="empty-state">
            <p>    </p>
          </div>
        ) : filteredTerms.length === 0 ? (
          <div className="empty-state">
            <p>   "{searchQuery}"   </p>
            <button
              onClick={() => setSearchQuery("")}
              style={{
                marginTop: '10px',
                padding: '8px 16px',
                background: '#667eea',
                color: 'white',
                border: 'none',
                borderRadius: '8px',
                cursor: 'pointer'
              }}
            >
               
            </button>
          </div>
        ) : (
          <div className="psychologists-list">
            {filteredTerms.map(term => (
              <div key={term.id} className="psychologist-item">
                <div className="psychologist-item-header">
                  <div style={{ display: 'flex', alignItems: 'center', gap: '15px' }}>
                    <span style={{ fontSize: '2rem', fontWeight: 'bold', color: '#667eea' }}>
                      {term.letter}
                    </span>
                    <div style={{ flex: 1 }}>
                      <div style={{ display: 'flex', alignItems: 'center', gap: '10px', marginBottom: '5px' }}>
                        <h3 style={{ margin: 0 }}>{term.term_ru}</h3>
                        <span style={{
                          padding: '4px 8px',
                          backgroundColor: term.order_index > 0 ? '#e3f2fd' : '#f5f5f5',
                          color: term.order_index > 0 ? '#1976d2' : '#666',
                          borderRadius: '4px',
                          fontSize: '11px',
                          fontWeight: 'bold',
                          border: `1px solid ${term.order_index > 0 ? '#90caf9' : '#ddd'}`
                        }}>
                          : {term.order_index || 0}
                        </span>
                      </div>
                      <p style={{ color: '#666', fontSize: '14px' }}>{term.term_kz}</p>
                      {term.definition_ru && (
                        <p style={{ color: '#888', fontSize: '13px', marginTop: '5px' }}>
                          {term.definition_ru.substring(0, 100)}...
                        </p>
                      )}
                    </div>
                  </div>
                </div>

                <div className="psychologist-item-content">
                  {term.image && (
                    <p style={{ marginTop: '10px', fontSize: '14px' }}>
                      <strong>:</strong> {term.image}
                    </p>
                  )}
                </div>

                <div className="psychologist-actions">
                  <button
                    onClick={() => editTerm(term)}
                    className="btn-secondary"
                    style={{
                      padding: '8px 16px',
                      background: '#ff9800',
                      color: 'white',
                      border: 'none',
                      borderRadius: '8px',
                      cursor: 'pointer',
                      fontSize: '14px'
                    }}
                  >
                     
                  </button>
                  <button
                    onClick={() => deleteTerm(term.id)}
                    className="btn-delete"
                    style={{
                      padding: '8px 16px',
                      background: '#f44336',
                      color: 'white',
                      border: 'none',
                      borderRadius: '8px',
                      cursor: 'pointer',
                      fontSize: '14px'
                    }}
                  >
                     
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}
      </section>
    </div>
  );
}

