import React, { useEffect, useState } from "react";
import { supabase } from "../../supabaseClient";
import "./AdminPanel.css";
import AppointmentsAdmin from "./AppointmentsAdmin";
import PsychologistsAdmin from "./PsychologistsAdmin";
import AlphabetAdmin from "./AlphabetAdmin";
import ExercisesAdmin from "./ExercisesAdmin";
import VideosAdmin from "./VideosAdmin";
import SurveysAdmin from "./SurveysAdmin";
import ContactsAdmin from "./ContactsAdmin";

export default function AdminPanel({ user: userProp }) {
  const [user, setUser] = useState(userProp || null);
  const [isAdmin, setIsAdmin] = useState(true); //    - 
  const [loading] = useState(false);
  const [activeTab, setActiveTab] = useState('appointments');

  const [news, setNews] = useState([]);
  const [title, setTitle] = useState("");
  const [shortContent, setShortContent] = useState("");
  const [fullContent, setFullContent] = useState("");
  const [imageUrl, setImageUrl] = useState("");
  const [link, setLink] = useState("");
  const [isMain, setIsMain] = useState(false);
  const [addingNews, setAddingNews] = useState(false);
  const [editingId, setEditingId] = useState(null); // ID ,  
  
  //    
  const [imageMode, setImageMode] = useState("url"); // "url"  "upload"
  const [imageFile, setImageFile] = useState(null);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [uploadedImageUrl, setUploadedImageUrl] = useState("");

  useEffect(() => {
    //    
    if (userProp) {
      setUser(userProp);
      setIsAdmin(true); //     admins - 
    }

    //   Supabase Auth    
    const clearSupabaseAuth = async () => {
      try {
        await supabase.auth.signOut();
        //   Supabase Auth   localStorage
        const keys = Object.keys(localStorage);
        keys.forEach(key => {
          if (key.includes('supabase') && key.includes('auth')) {
            localStorage.removeItem(key);
          }
        });
      } catch (e) {
        //  
      }
    };

    //    Supabase
    const loadNews = async () => {
      //    
      await clearSupabaseAuth();
      
      const { data, error } = await supabase
        .from('news')
        .select('*')
        .order('created_at', { ascending: false });
      
      if (error) {
        console.error('  :', error);
        //   JWT,     
        if (error.code === 'PGRST303' || error.message?.includes('JWT')) {
          await clearSupabaseAuth();
          const { data: retryData, error: retryError } = await supabase
            .from('news')
            .select('*')
            .order('created_at', { ascending: false });
          
          if (!retryError && retryData) {
            setNews(retryData);
          }
        }
      } else {
        setNews(data || []);
      }
    };

    loadNews();

    //      news
    const newsSubscription = supabase
      .channel('news_changes')
      .on('postgres_changes', 
        { event: '*', schema: 'public', table: 'news' },
        () => {
          loadNews(); //    
        }
      )
      .subscribe();

    return () => {
      newsSubscription.unsubscribe();
    };
  }, [userProp]);

  //       ( ,   )
  const getSafeFileName = (originalName) => {
    //   
    const extension = originalName.substring(originalName.lastIndexOf('.'));
    
    //    
    const nameWithoutExt = originalName.substring(0, originalName.lastIndexOf('.'));
    
    //  :  ,      
    const transliterate = (str) => {
      const ru = '';
      const en = 'abvgdeezziyklmnoprstufhccss_y_eua';
      
      return str.toLowerCase().split('').map(char => {
        const index = ru.indexOf(char);
        return index !== -1 ? en[index] : char;
      }).join('');
    };
    
    //   
    const safeName = transliterate(nameWithoutExt)
      .replace(/[^\w.-]/g, '_') //   -,   ( . -)  _
      .replace(/_+/g, '_') //    
      .replace(/^_|_$/g, '') //      
      .toLowerCase();
    
    //   ,  "image"
    const finalName = safeName || 'image';
    
    return `${finalName}${extension}`;
  };

  //    Supabase Storage
  const uploadImage = async (file) => {
    if (!file) {
      throw new Error("  ");
    }

    //   
    if (!file.type.startsWith('image/')) {
      throw new Error(",  ");
    }

    try {
      //       
      const timestamp = Date.now();
      const safeFileName = getSafeFileName(file.name);
      const fileName = `news/${timestamp}_${safeFileName}`;

      //    Supabase Storage
      const { error } = await supabase.storage
        .from('news-images')
        .upload(fileName, file);

      if (error) {
        throw error;
      }

      //   URL
      const { data: publicUrl } = supabase.storage
        .from('news-images')
        .getPublicUrl(fileName);

      setUploadProgress(0);
      return publicUrl.publicUrl;
    } catch (error) {
      console.error(" :", error);
      throw error;
    }
  };

  const addNews = async (e) => {
    e.preventDefault();
    if (!isAdmin) return alert(" ");
    if (!title || !shortContent || !fullContent) return alert("  ");
    
    setAddingNews(true);
    try {
      let finalImageUrl = "";

      //  URL     
      if (imageMode === "upload" && imageFile) {
        //    Supabase Storage
        finalImageUrl = await uploadImage(imageFile);
      } else if (imageMode === "url" && imageUrl.trim()) {
        //   URL
        finalImageUrl = imageUrl.trim();
      }

      //      ,     
      if (isMain) {
        const { error: updateError } = await supabase
          .from('news')
          .update({ is_main: false })
          .eq('is_main', true)
          .neq('id', editingId || 0); //    
        
        if (updateError) {
          console.error('   :', updateError);
          //   
        }
      }

      const newsData = {
        title,
        short_content: shortContent,
        full_content: fullContent,
        link: link.trim() || null,
        is_main: isMain,
      };

      //   URL ,  
      if (finalImageUrl) {
        newsData.image_url = finalImageUrl;
      }

      let error;
      if (editingId) {
        //   
        const { error: updateError } = await supabase
          .from('news')
          .update(newsData)
          .eq('id', editingId);
        error = updateError;
      } else {
        //   
        newsData.created_at = new Date().toISOString();
        const { error: insertError } = await supabase
          .from('news')
          .insert(newsData);
        error = insertError;
      }

      if (error) {
        throw error;
      }
      
      //  
      resetForm();
      
      alert(editingId ? "   !" : "   !");
    } catch (error) {
      alert(`  ${editingId ? '' : ''} : ` + error.message);
      console.error(error);
    } finally {
      setAddingNews(false);
    }
  };

  //       
  const editNews = (newsItem) => {
    setEditingId(newsItem.id);
    setTitle(newsItem.title || "");
    setShortContent(newsItem.short_content || newsItem.content || "");
    setFullContent(newsItem.full_content || newsItem.content || "");
    setImageUrl(newsItem.image_url || "");
    setLink(newsItem.link || "");
    setIsMain(newsItem.is_main || false);
    setImageMode("url"); //   
    setImageFile(null);
    setUploadedImageUrl("");
    setUploadProgress(0);
    
    //   
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  //     ( )
  const resetForm = () => {
    setEditingId(null);
    setTitle("");
    setShortContent("");
    setFullContent("");
    setImageUrl("");
    setLink("");
    setIsMain(false);
    setImageMode("url");
    setImageFile(null);
    setUploadedImageUrl("");
    setUploadProgress(0);
  };

  const remove = async (id) => {
    if (!isAdmin) return alert(" ");
    if (!window.confirm(" ,     ?")) return;
    
    try {
      const { error } = await supabase
        .from('news')
        .delete()
        .eq('id', id);

      if (error) {
        throw error;
      }
    } catch (error) {
      alert("  : " + error.message);
    }
  };

  const setAsMain = async (id) => {
    if (!isAdmin) return alert(" ");
    if (!window.confirm("   ?     .")) return;
    
    try {
      //      
      const { error: updateError } = await supabase
        .from('news')
        .update({ is_main: false })
        .eq('is_main', true);
      
      if (updateError) {
        console.error('   :', updateError);
      }

      //      
      const { error } = await supabase
        .from('news')
        .update({ is_main: true })
        .eq('id', id);

      if (error) {
        throw error;
      }

      alert("   !");
    } catch (error) {
      alert("    : " + error.message);
    }
  };

  const logout = () => {
    //      localStorage
    localStorage.removeItem('adminToken');
    localStorage.removeItem('adminData');
    //       
    window.location.reload();
    window.location.reload();
  };

  if (loading) {
    return (
      <div className="admin-loading">
        <div className="loading-spinner"></div>
        <p>...</p>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="admin-error">
        <h3>  </h3>
        <p>,   </p>
      </div>
    );
  }

  if (!isAdmin) {
    return (
      <div className="admin-error">
        <h3>  </h3>
        <p>    </p>
        <button onClick={logout} className="btn btn-secondary"></button>
      </div>
    );
  }

  return (
    <div className="admin-panel">
      <div className="admin-header">
        <div className="container">
          <div className="admin-header-content">
            <div>
              <h1> </h1>
              <p> , {user.fullName || user.username}</p>
            </div>
            <button onClick={logout} className="btn btn-secondary">
              
            </button>
          </div>
        </div>
      </div>

      <div className="admin-content">
        <div className="container">
          {/*    */}
          <div className="admin-tabs" style={{ 
            display: 'flex', 
            gap: '10px', 
            marginBottom: '30px', 
            borderBottom: '2px solid #e0e0e0',
            flexWrap: 'wrap'
          }}>
            <button
              onClick={() => setActiveTab('appointments')}
              className={activeTab === 'appointments' ? 'active' : ''}
              style={{
                padding: '12px 20px',
                border: 'none',
                background: activeTab === 'appointments' ? '#667eea' : 'transparent',
                color: activeTab === 'appointments' ? 'white' : '#666',
                cursor: 'pointer',
                borderBottom: activeTab === 'appointments' ? '3px solid #667eea' : '3px solid transparent',
                fontWeight: activeTab === 'appointments' ? 'bold' : 'normal',
                transition: 'all 0.3s'
              }}
            >
               
            </button>
            <button
              onClick={() => setActiveTab('psychologists')}
              className={activeTab === 'psychologists' ? 'active' : ''}
              style={{
                padding: '12px 20px',
                border: 'none',
                background: activeTab === 'psychologists' ? '#667eea' : 'transparent',
                color: activeTab === 'psychologists' ? 'white' : '#666',
                cursor: 'pointer',
                borderBottom: activeTab === 'psychologists' ? '3px solid #667eea' : '3px solid transparent',
                fontWeight: activeTab === 'psychologists' ? 'bold' : 'normal',
                transition: 'all 0.3s'
              }}
            >
               
            </button>
            <button
              onClick={() => setActiveTab('news')}
              className={activeTab === 'news' ? 'active' : ''}
              style={{
                padding: '12px 20px',
                border: 'none',
                background: activeTab === 'news' ? '#667eea' : 'transparent',
                color: activeTab === 'news' ? 'white' : '#666',
                cursor: 'pointer',
                borderBottom: activeTab === 'news' ? '3px solid #667eea' : '3px solid transparent',
                fontWeight: activeTab === 'news' ? 'bold' : 'normal',
                transition: 'all 0.3s'
              }}
            >
               
            </button>
            <button
              onClick={() => setActiveTab('alphabet')}
              className={activeTab === 'alphabet' ? 'active' : ''}
              style={{
                padding: '12px 20px',
                border: 'none',
                background: activeTab === 'alphabet' ? '#667eea' : 'transparent',
                color: activeTab === 'alphabet' ? 'white' : '#666',
                cursor: 'pointer',
                borderBottom: activeTab === 'alphabet' ? '3px solid #667eea' : '3px solid transparent',
                fontWeight: activeTab === 'alphabet' ? 'bold' : 'normal',
                transition: 'all 0.3s'
              }}
            >
               
            </button>
            <button
              onClick={() => setActiveTab('exercises')}
              className={activeTab === 'exercises' ? 'active' : ''}
              style={{
                padding: '12px 20px',
                border: 'none',
                background: activeTab === 'exercises' ? '#667eea' : 'transparent',
                color: activeTab === 'exercises' ? 'white' : '#666',
                cursor: 'pointer',
                borderBottom: activeTab === 'exercises' ? '3px solid #667eea' : '3px solid transparent',
                fontWeight: activeTab === 'exercises' ? 'bold' : 'normal',
                transition: 'all 0.3s'
              }}
            >
               
            </button>
            <button
              onClick={() => setActiveTab('videos')}
              className={activeTab === 'videos' ? 'active' : ''}
              style={{
                padding: '12px 20px',
                border: 'none',
                background: activeTab === 'videos' ? '#667eea' : 'transparent',
                color: activeTab === 'videos' ? 'white' : '#666',
                cursor: 'pointer',
                borderBottom: activeTab === 'videos' ? '3px solid #667eea' : '3px solid transparent',
                fontWeight: activeTab === 'videos' ? 'bold' : 'normal',
                transition: 'all 0.3s'
              }}
            >
               
            </button>
            <button
              onClick={() => setActiveTab('surveys')}
              className={activeTab === 'surveys' ? 'active' : ''}
              style={{
                padding: '12px 20px',
                border: 'none',
                background: activeTab === 'surveys' ? '#667eea' : 'transparent',
                color: activeTab === 'surveys' ? 'white' : '#666',
                cursor: 'pointer',
                borderBottom: activeTab === 'surveys' ? '3px solid #667eea' : '3px solid transparent',
                fontWeight: activeTab === 'surveys' ? 'bold' : 'normal',
                transition: 'all 0.3s'
              }}
            >
               
            </button>
            <button
              onClick={() => setActiveTab('contacts')}
              className={activeTab === 'contacts' ? 'active' : ''}
              style={{
                padding: '12px 20px',
                border: 'none',
                background: activeTab === 'contacts' ? '#667eea' : 'transparent',
                color: activeTab === 'contacts' ? 'white' : '#666',
                cursor: 'pointer',
                borderBottom: activeTab === 'contacts' ? '3px solid #667eea' : '3px solid transparent',
                fontWeight: activeTab === 'contacts' ? 'bold' : 'normal',
                transition: 'all 0.3s'
              }}
            >
               
            </button>
          </div>

          {/*   */}
          {activeTab === 'appointments' && <AppointmentsAdmin />}
          {activeTab === 'psychologists' && <PsychologistsAdmin />}
          {activeTab === 'alphabet' && <AlphabetAdmin />}
          {activeTab === 'exercises' && <ExercisesAdmin />}
          {activeTab === 'videos' && <VideosAdmin />}
          {activeTab === 'surveys' && <SurveysAdmin />}
          {activeTab === 'contacts' && <ContactsAdmin />}

          {/*  /  */}
          {activeTab === 'news' && (
            <>
          <section className="admin-section">
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '20px' }}>
              <h2 className="section-title">
                {editingId ? '  ' : '  '}
              </h2>
              {editingId && (
                <button 
                  type="button"
                  onClick={resetForm}
                  className="btn btn-secondary"
                  style={{ padding: '8px 16px', fontSize: '14px' }}
                >
                    
                </button>
              )}
            </div>
            <div className="add-news-card">
              <form onSubmit={addNews} className="news-form">
                <div className="form-group">
                  <label htmlFor="news-title"></label>
                  <input
                    id="news-title"
                    type="text"
                    value={title}
                    onChange={e => setTitle(e.target.value)}
                    placeholder="  "
                    required
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="news-short-content">  (  )</label>
                  <textarea
                    id="news-short-content"
                    value={shortContent}
                    onChange={e => setShortContent(e.target.value)}
                    placeholder="    (2-3 ).          ."
                    rows="4"
                    required
                    style={{ minHeight: '100px', fontSize: '14px' }}
                  />
                  <small style={{ color: '#666', fontSize: '12px', marginTop: '5px', display: 'block' }}>
                           
                  </small>
                </div>

                <div className="form-group">
                  <label htmlFor="news-full-content">  (  "")</label>
                  <textarea
                    id="news-full-content"
                    value={fullContent}
                    onChange={e => setFullContent(e.target.value)}
                    placeholder="   .           ."
                    rows="15"
                    required
                    style={{ minHeight: '300px', fontSize: '14px' }}
                  />
                  <small style={{ color: '#666', fontSize: '12px', marginTop: '5px', display: 'block' }}>
                     :        
                  </small>
                </div>

                <div className="form-group">
                  <label style={{ display: 'flex', alignItems: 'center', gap: '10px', cursor: 'pointer' }}>
                    <input
                      type="checkbox"
                      checked={isMain}
                      onChange={(e) => setIsMain(e.target.checked)}
                      style={{ width: '18px', height: '18px', cursor: 'pointer' }}
                    />
                    <span style={{ fontWeight: '500' }}>   </span>
                  </label>
                  <small style={{ color: '#666', fontSize: '12px', marginTop: '5px', display: 'block', marginLeft: '28px' }}>
                      ,         .      .
                  </small>
                </div>

                <div className="form-group">
                  <label> ()</label>
                  
                  {/*   */}
                  <div style={{ marginBottom: '15px', display: 'flex', gap: '10px' }}>
                    <button
                      type="button"
                      onClick={() => setImageMode("url")}
                      className={`mode-btn ${imageMode === "url" ? "active" : ""}`}
                      style={{
                        padding: '8px 16px',
                        border: imageMode === "url" ? '2px solid #667eea' : '2px solid #ddd',
                        borderRadius: '8px',
                        background: imageMode === "url" ? '#f0f2ff' : 'white',
                        color: imageMode === "url" ? '#667eea' : '#666',
                        cursor: 'pointer',
                        fontWeight: imageMode === "url" ? 'bold' : 'normal',
                        transition: 'all 0.3s'
                      }}
                    >
                        URL
                    </button>
                    <button
                      type="button"
                      onClick={() => setImageMode("upload")}
                      className={`mode-btn ${imageMode === "upload" ? "active" : ""}`}
                      style={{
                        padding: '8px 16px',
                        border: imageMode === "upload" ? '2px solid #667eea' : '2px solid #ddd',
                        borderRadius: '8px',
                        background: imageMode === "upload" ? '#f0f2ff' : 'white',
                        color: imageMode === "upload" ? '#667eea' : '#666',
                        cursor: 'pointer',
                        fontWeight: imageMode === "upload" ? 'bold' : 'normal',
                        transition: 'all 0.3s'
                      }}
                    >
                        
                    </button>
                  </div>

                  {/*  URL */}
                  {imageMode === "url" && (
                    <>
                      <input
                        id="news-image-url"
                        type="url"
                        value={imageUrl}
                        onChange={e => setImageUrl(e.target.value)}
                        placeholder="https://example.com/image.jpg"
                        style={{
                          width: '100%',
                          padding: '12px',
                          border: '1px solid #ddd',
                          borderRadius: '8px',
                          fontSize: '14px'
                        }}
                      />
                      <p className="field-hint" style={{ marginTop: '8px', fontSize: '13px', color: '#666' }}>
                            <a href="https://imgur.com/upload" target="_blank" rel="noopener noreferrer">Imgur</a>  <a href="https://cloudinary.com" target="_blank" rel="noopener noreferrer">Cloudinary</a>
                      </p>
                    </>
                  )}

                  {/*    */}
                  {imageMode === "upload" && (
                    <>
                      <input
                        id="news-image-file"
                        type="file"
                        accept="image/*"
                        onChange={(e) => {
                          const file = e.target.files[0];
                          setImageFile(file);
                          if (file) {
                            //  
                            const reader = new FileReader();
                            reader.onloadend = () => {
                              setUploadedImageUrl(reader.result);
                            };
                            reader.readAsDataURL(file);
                          }
                        }}
                        style={{
                          width: '100%',
                          padding: '12px',
                          border: '2px dashed #667eea',
                          borderRadius: '8px',
                          fontSize: '14px',
                          background: '#f8f9ff'
                        }}
                      />
                      <p className="field-hint" style={{ marginTop: '8px', fontSize: '13px', color: '#666' }}>
                              (JPG, PNG, GIF)
                      </p>

                      {/*   */}
                      {uploadedImageUrl && (
                        <div style={{ marginTop: '15px' }}>
                          <p style={{ fontSize: '13px', color: '#666', marginBottom: '8px' }}>:</p>
                          <img
                            src={uploadedImageUrl}
                            alt=""
                            style={{
                              maxWidth: '300px',
                              maxHeight: '200px',
                              borderRadius: '8px',
                              border: '1px solid #ddd'
                            }}
                          />
                        </div>
                      )}

                      {/* -  */}
                      {uploadProgress > 0 && uploadProgress < 100 && (
                        <div style={{ marginTop: '15px' }}>
                          <p style={{ fontSize: '13px', color: '#666', marginBottom: '8px' }}>
                            : {Math.round(uploadProgress)}%
                          </p>
                          <div style={{
                            width: '100%',
                            height: '8px',
                            background: '#e0e0e0',
                            borderRadius: '4px',
                            overflow: 'hidden'
                          }}>
                            <div style={{
                              width: `${uploadProgress}%`,
                              height: '100%',
                              background: 'linear-gradient(90deg, #667eea, #764ba2)',
                              transition: 'width 0.3s'
                            }}></div>
                          </div>
                        </div>
                      )}
                    </>
                  )}
                </div>

                <div className="form-group">
                  <label htmlFor="news-link">  ()</label>
                  <input
                    id="news-link"
                    type="url"
                    value={link}
                    onChange={e => setLink(e.target.value)}
                    placeholder="https://example.com"
                  />
                  <p className="field-hint">   , , Instagram  ..</p>
                </div>

                <button 
                  type="submit" 
                  className="btn btn-primary"
                  disabled={addingNews || (uploadProgress > 0 && uploadProgress < 100)}
                >
                  {uploadProgress > 0 && uploadProgress < 100
                    ? `  ${Math.round(uploadProgress)}%...`
                    : addingNews
                    ? (editingId ? " ..." : " ...")
                    : (editingId ? "  " : "  ")}
                </button>
              </form>
            </div>
          </section>

          {/*   */}
          <section className="admin-section">
            <h2 className="section-title">
                ({news.length})
            </h2>
            
            {news.length === 0 ? (
              <div className="empty-state">
                <p>    </p>
              </div>
            ) : (
              <div className="news-list">
                {news.map(n => (
                  <div key={n.id} className="news-item">
                    <div className="news-item-header">
                      <h3>{n.title}</h3>
                      <span className="news-date">
                        {n.created_at ? new Date(n.created_at).toLocaleString('ru-RU') : " "}
                      </span>
                    </div>
                    
                    {n.image_url && (
                      <div className="news-item-image">
                        <img src={n.image_url} alt={n.title} />
                      </div>
                    )}
                    
                    <p className="news-text">{n.short_content || n.content || ' '}</p>
                    
                    {n.link && (
                      <div className="news-item-link">
                        <a href={n.link} target="_blank" rel="noopener noreferrer" className="external-link">
                              
                        </a>
                      </div>
                    )}
                    
                    <div className="news-actions">
                      <button 
                        onClick={() => editNews(n)} 
                        className="btn-secondary"
                        style={{ 
                          marginRight: '10px',
                          padding: '8px 16px',
                          background: '#ff9800',
                          color: 'white',
                          border: 'none',
                          borderRadius: '8px',
                          cursor: 'pointer',
                          fontSize: '14px'
                        }}
                      >
                         
                      </button>
                      {n.is_main ? (
                        <span style={{ 
                          marginRight: '10px',
                          padding: '8px 16px', 
                          background: '#4CAF50', 
                          color: 'white', 
                          borderRadius: '8px',
                          fontWeight: 'bold',
                          fontSize: '14px'
                        }}>
                            
                        </span>
                      ) : (
                        <button 
                          onClick={() => setAsMain(n.id)} 
                          className="btn-secondary"
                          style={{ 
                            marginRight: '10px',
                            padding: '8px 16px',
                            background: '#667eea',
                            color: 'white',
                            border: 'none',
                            borderRadius: '8px',
                            cursor: 'pointer',
                            fontSize: '14px'
                          }}
                        >
                            
                        </button>
                      )}
                      <button 
                        onClick={() => remove(n.id)} 
                        className="btn-delete"
                      >
                         
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </section>
            </>
          )}
        </div>
      </div>
    </div>
  );
}

