import React, { useEffect, useState } from "react";
import { supabase } from "../../supabaseClient";
import "./PsychologistsAdmin.css";

export default function SurveysAdmin() {
  const [surveys, setSurveys] = useState([]);
  const [loading, setLoading] = useState(true);
  const [addingSurvey, setAddingSurvey] = useState(false);
  const [editingId, setEditingId] = useState(null);

  //  
  const [icon, setIcon] = useState("");
  const [titleRu, setTitleRu] = useState("");
  const [titleKz, setTitleKz] = useState("");
  const [descriptionRu, setDescriptionRu] = useState("");
  const [descriptionKz, setDescriptionKz] = useState("");
  const [duration, setDuration] = useState("10");
  const [questions, setQuestions] = useState("10");
  const [externalLink, setExternalLink] = useState("");
  const [orderIndex, setOrderIndex] = useState(0);

  useEffect(() => {
    loadSurveys();
  }, []);

  const loadSurveys = async () => {
    try {
      setLoading(true);
      const { data, error } = await supabase
        .from('surveys')
        .select('*')
        .order('order_index', { ascending: true });

      if (error) {
        throw error;
      }

      setSurveys(data || []);
    } catch (error) {
      console.error('  :', error);
      alert("  : " + error.message);
    } finally {
      setLoading(false);
    }
  };

  const resetForm = () => {
    setEditingId(null);
    setIcon("");
    setTitleRu("");
    setTitleKz("");
    setDescriptionRu("");
    setDescriptionKz("");
    setDuration("10");
    setQuestions("10");
    setExternalLink("");
    setOrderIndex(0);
  };

  const editSurvey = (survey) => {
    setEditingId(survey.id);
    setIcon(survey.icon || "");
    setTitleRu(survey.title_ru || "");
    setTitleKz(survey.title_kz || "");
    setDescriptionRu(survey.description_ru || "");
    setDescriptionKz(survey.description_kz || "");
    setDuration(survey.duration || "10");
    setQuestions(survey.questions || "10");
    setExternalLink(survey.external_link || "");
    setOrderIndex(survey.order_index || 0);
    
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const saveSurvey = async (e) => {
    e.preventDefault();
    
    if (!titleRu || !titleKz) {
      return alert("      ");
    }

    setAddingSurvey(true);
    try {
      const surveyData = {
        icon: icon || "",
        title_ru: titleRu.trim(),
        title_kz: titleKz.trim(),
        description_ru: descriptionRu.trim() || null,
        description_kz: descriptionKz.trim() || null,
        duration: duration ? parseInt(duration) : 10,
        questions: questions ? parseInt(questions) : 10,
        external_link: externalLink.trim() || null,
        order_index: orderIndex || 0,
      };

      let error;
      if (editingId) {
        const { error: updateError } = await supabase
          .from('surveys')
          .update(surveyData)
          .eq('id', editingId);
        error = updateError;
      } else {
        const { error: insertError } = await supabase
          .from('surveys')
          .insert(surveyData);
        error = insertError;
      }

      if (error) {
        throw error;
      }

      resetForm();
      loadSurveys();
      alert(editingId ? " !" : " !");
    } catch (error) {
      alert("  : " + error.message);
      console.error(error);
    } finally {
      setAddingSurvey(false);
    }
  };

  const deleteSurvey = async (id) => {
    if (!window.confirm(" ,     ?")) {
      return;
    }

    try {
      const { error } = await supabase
        .from('surveys')
        .delete()
        .eq('id', id);

      if (error) {
        throw error;
      }

      loadSurveys();
      alert(" !");
    } catch (error) {
      alert("  : " + error.message);
      console.error(error);
    }
  };

  if (loading) {
    return (
      <div style={{ textAlign: 'center', padding: '2rem' }}>
        <p> ...</p>
      </div>
    );
  }

  return (
    <div>
      <section className="admin-section">
        <h2 className="section-title">
          {editingId ? "  " : "  "}
        </h2>
        <form onSubmit={saveSurvey}>
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', marginBottom: '20px' }}>
            <div className="form-group">
              <label htmlFor="icon"> ()</label>
              <input
                id="icon"
                type="text"
                value={icon}
                onChange={e => setIcon(e.target.value)}
                placeholder=""
                maxLength="2"
              />
            </div>

            <div className="form-group">
              <label htmlFor="order-index"> </label>
              <input
                id="order-index"
                type="number"
                value={orderIndex}
                onChange={e => setOrderIndex(parseInt(e.target.value) || 0)}
                min="0"
              />
            </div>
          </div>

          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', marginBottom: '20px' }}>
            <div className="form-group">
              <label htmlFor="title-ru"> () *</label>
              <input
                id="title-ru"
                type="text"
                value={titleRu}
                onChange={e => setTitleRu(e.target.value)}
                placeholder="   "
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="title-kz"> () *</label>
              <input
                id="title-kz"
                type="text"
                value={titleKz}
                onChange={e => setTitleKz(e.target.value)}
                placeholder="  "
                required
              />
            </div>
          </div>

          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', marginBottom: '20px' }}>
            <div className="form-group">
              <label htmlFor="description-ru"> ()</label>
              <textarea
                id="description-ru"
                value={descriptionRu}
                onChange={e => setDescriptionRu(e.target.value)}
                placeholder="  ,    ..."
                rows="4"
              />
            </div>

            <div className="form-group">
              <label htmlFor="description-kz"> ()</label>
              <textarea
                id="description-kz"
                value={descriptionKz}
                onChange={e => setDescriptionKz(e.target.value)}
                placeholder=",      ..."
                rows="4"
              />
            </div>
          </div>

          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr', gap: '20px', marginBottom: '20px' }}>
            <div className="form-group">
              <label htmlFor="duration"> ()</label>
              <input
                id="duration"
                type="number"
                value={duration}
                onChange={e => setDuration(e.target.value)}
                min="1"
              />
            </div>

            <div className="form-group">
              <label htmlFor="questions"> </label>
              <input
                id="questions"
                type="number"
                value={questions}
                onChange={e => setQuestions(e.target.value)}
                min="1"
              />
            </div>

            <div className="form-group">
              <label htmlFor="external-link"> </label>
              <input
                id="external-link"
                type="url"
                value={externalLink}
                onChange={e => setExternalLink(e.target.value)}
                placeholder="https://psytests.org/..."
              />
            </div>
          </div>

          <button
            type="submit"
            className="btn btn-primary"
            disabled={addingSurvey}
          >
            {addingSurvey
              ? (editingId ? "..." : "...")
              : (editingId ? "  " : "  ")}
          </button>
          {editingId && (
            <button
              type="button"
              onClick={resetForm}
              className="btn-secondary"
              style={{ marginLeft: '10px' }}
            >
              
            </button>
          )}
        </form>
      </section>

      <section className="admin-section">
        <h2 className="section-title">
            ({surveys.length})
        </h2>

        {surveys.length === 0 ? (
          <div className="empty-state">
            <p>    </p>
          </div>
        ) : (
          <div className="psychologists-list">
            {surveys.map(survey => (
              <div key={survey.id} className="psychologist-item">
                <div className="psychologist-item-header">
                  <div style={{ display: 'flex', alignItems: 'center', gap: '15px' }}>
                    <span style={{ fontSize: '2rem' }}>{survey.icon || ""}</span>
                    <div>
                      <h3>{survey.title_ru}</h3>
                      <p style={{ color: '#666', fontSize: '14px' }}>{survey.title_kz}</p>
                      <p style={{ color: '#888', fontSize: '13px', marginTop: '5px' }}>
                         {survey.duration}  |  {survey.questions} 
                      </p>
                    </div>
                  </div>
                </div>

                <div className="psychologist-item-content">
                  {survey.description_ru && (
                    <p style={{ marginTop: '10px', fontSize: '14px', color: '#666' }}>
                      {survey.description_ru.substring(0, 200)}...
                    </p>
                  )}
                  {survey.external_link && (
                    <p style={{ marginTop: '10px', fontSize: '14px' }}>
                      <strong>:</strong> <a href={survey.external_link} target="_blank" rel="noopener noreferrer">{survey.external_link}</a>
                    </p>
                  )}
                </div>

                <div className="psychologist-actions">
                  <button
                    onClick={() => editSurvey(survey)}
                    className="btn-secondary"
                    style={{
                      padding: '8px 16px',
                      background: '#ff9800',
                      color: 'white',
                      border: 'none',
                      borderRadius: '8px',
                      cursor: 'pointer',
                      fontSize: '14px'
                    }}
                  >
                     
                  </button>
                  <button
                    onClick={() => deleteSurvey(survey.id)}
                    className="btn-delete"
                    style={{
                      padding: '8px 16px',
                      background: '#f44336',
                      color: 'white',
                      border: 'none',
                      borderRadius: '8px',
                      cursor: 'pointer',
                      fontSize: '14px'
                    }}
                  >
                     
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}
      </section>
    </div>
  );
}

