import React, { useEffect, useState } from "react";
import { supabase } from "../../supabaseClient";
import "./PsychologistsAdmin.css";

export default function VideosAdmin() {
  const [videos, setVideos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [addingVideo, setAddingVideo] = useState(false);
  const [editingId, setEditingId] = useState(null);

  //  
  const [titleRu, setTitleRu] = useState("");
  const [titleKz, setTitleKz] = useState("");
  const [descriptionRu, setDescriptionRu] = useState("");
  const [descriptionKz, setDescriptionKz] = useState("");
  const [url, setUrl] = useState("");
  const [platform, setPlatform] = useState("YouTube");
  const [orderIndex, setOrderIndex] = useState(0);

  useEffect(() => {
    loadVideos();
  }, []);

  const loadVideos = async () => {
    try {
      setLoading(true);
      
      //   Supabase Auth 
      try {
        await supabase.auth.signOut();
        const keys = Object.keys(localStorage);
        keys.forEach(key => {
          if (key.includes('supabase') && key.includes('auth')) {
            localStorage.removeItem(key);
          }
        });
      } catch (e) {
        //  
      }
      
      const { data, error } = await supabase
        .from('videos')
        .select('*')
        .order('order_index', { ascending: true });

      if (error) {
        throw error;
      }

      setVideos(data || []);
    } catch (error) {
      console.error('  :', error);
      alert("  : " + error.message);
    } finally {
      setLoading(false);
    }
  };

  const resetForm = () => {
    setEditingId(null);
    setTitleRu("");
    setTitleKz("");
    setDescriptionRu("");
    setDescriptionKz("");
    setUrl("");
    setPlatform("YouTube");
    setOrderIndex(0);
  };

  const editVideo = (video) => {
    setEditingId(video.id);
    setTitleRu(video.title_ru || "");
    setTitleKz(video.title_kz || "");
    setDescriptionRu(video.description_ru || "");
    setDescriptionKz(video.description_kz || "");
    setUrl(video.url || "");
    setPlatform(video.platform || "YouTube");
    setOrderIndex(video.order_index || 0);
    
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const saveVideo = async (e) => {
    e.preventDefault();
    
    if (!titleRu || !titleKz || !url) {
      return alert("      URL");
    }

    setAddingVideo(true);
    try {
      //   Supabase Auth   
      try {
        await supabase.auth.signOut();
        const keys = Object.keys(localStorage);
        keys.forEach(key => {
          if (key.includes('supabase') && key.includes('auth')) {
            localStorage.removeItem(key);
          }
        });
      } catch (e) {
        //  
      }
      
      const videoData = {
        title_ru: titleRu.trim(),
        title_kz: titleKz.trim(),
        description_ru: descriptionRu.trim() || null,
        description_kz: descriptionKz.trim() || null,
        url: url.trim(),
        platform: platform || "YouTube",
        order_index: orderIndex || 0,
      };

      let error;
      if (editingId) {
        const { error: updateError } = await supabase
          .from('videos')
          .update(videoData)
          .eq('id', editingId);
        error = updateError;
      } else {
        const { error: insertError } = await supabase
          .from('videos')
          .insert(videoData);
        error = insertError;
      }

      if (error) {
        throw error;
      }

      resetForm();
      loadVideos();
      alert(editingId ? " !" : " !");
    } catch (error) {
      alert("  : " + error.message);
      console.error(error);
    } finally {
      setAddingVideo(false);
    }
  };

  const deleteVideo = async (id) => {
    if (!window.confirm(" ,     ?")) {
      return;
    }

    try {
      //   Supabase Auth   
      try {
        await supabase.auth.signOut();
        const keys = Object.keys(localStorage);
        keys.forEach(key => {
          if (key.includes('supabase') && key.includes('auth')) {
            localStorage.removeItem(key);
          }
        });
      } catch (e) {
        //  
      }
      
      const { error } = await supabase
        .from('videos')
        .delete()
        .eq('id', id);

      if (error) {
        throw error;
      }

      loadVideos();
      alert(" !");
    } catch (error) {
      alert("  : " + error.message);
      console.error(error);
    }
  };

  if (loading) {
    return (
      <div style={{ textAlign: 'center', padding: '2rem' }}>
        <p> ...</p>
      </div>
    );
  }

  return (
    <div>
      <section className="admin-section">
        <h2 className="section-title">
          {editingId ? "  " : "  "}
        </h2>
        <form onSubmit={saveVideo}>
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', marginBottom: '20px' }}>
            <div className="form-group">
              <label htmlFor="title-ru"> () *</label>
              <input
                id="title-ru"
                type="text"
                value={titleRu}
                onChange={e => setTitleRu(e.target.value)}
                placeholder="     "
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="title-kz"> () *</label>
              <input
                id="title-kz"
                type="text"
                value={titleKz}
                onChange={e => setTitleKz(e.target.value)}
                placeholder="    "
                required
              />
            </div>
          </div>

          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', marginBottom: '20px' }}>
            <div className="form-group">
              <label htmlFor="description-ru"> ()</label>
              <textarea
                id="description-ru"
                value={descriptionRu}
                onChange={e => setDescriptionRu(e.target.value)}
                placeholder="      ..."
                rows="4"
              />
            </div>

            <div className="form-group">
              <label htmlFor="description-kz"> ()</label>
              <textarea
                id="description-kz"
                value={descriptionKz}
                onChange={e => setDescriptionKz(e.target.value)}
                placeholder="     ..."
                rows="4"
              />
            </div>
          </div>

          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr', gap: '20px', marginBottom: '20px' }}>
            <div className="form-group">
              <label htmlFor="url">URL  *</label>
              <input
                id="url"
                type="url"
                value={url}
                onChange={e => setUrl(e.target.value)}
                placeholder="https://www.youtube.com/watch?v=..."
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="platform"></label>
              <select
                id="platform"
                value={platform}
                onChange={e => setPlatform(e.target.value)}
              >
                <option value="YouTube">YouTube</option>
                <option value="Vimeo">Vimeo</option>
                <option value="">Other / </option>
              </select>
            </div>

            <div className="form-group">
              <label htmlFor="order-index"> </label>
              <input
                id="order-index"
                type="number"
                value={orderIndex}
                onChange={e => setOrderIndex(parseInt(e.target.value) || 0)}
                min="0"
              />
            </div>
          </div>

          <button
            type="submit"
            className="btn btn-primary"
            disabled={addingVideo}
          >
            {addingVideo
              ? (editingId ? "..." : "...")
              : (editingId ? "  " : "  ")}
          </button>
          {editingId && (
            <button
              type="button"
              onClick={resetForm}
              className="btn-secondary"
              style={{ marginLeft: '10px' }}
            >
              
            </button>
          )}
        </form>
      </section>

      <section className="admin-section">
        <h2 className="section-title">
            ({videos.length})
        </h2>

        {videos.length === 0 ? (
          <div className="empty-state">
            <p>    </p>
          </div>
        ) : (
          <div className="psychologists-list">
            {videos.map(video => (
              <div key={video.id} className="psychologist-item">
                <div className="psychologist-item-header">
                  <div>
                    <h3>{video.title_ru}</h3>
                    <p style={{ color: '#666', fontSize: '14px' }}>{video.title_kz}</p>
                    <p style={{ color: '#888', fontSize: '13px', marginTop: '5px' }}>
                       {video.platform}
                    </p>
                  </div>
                </div>

                <div className="psychologist-item-content">
                  {video.description_ru && (
                    <p style={{ marginTop: '10px', fontSize: '14px', color: '#666' }}>
                      {video.description_ru.substring(0, 200)}...
                    </p>
                  )}
                  {video.url && (
                    <p style={{ marginTop: '10px', fontSize: '14px' }}>
                      <strong>:</strong> <a href={video.url} target="_blank" rel="noopener noreferrer">{video.url.substring(0, 60)}...</a>
                    </p>
                  )}
                </div>

                <div className="psychologist-actions">
                  <button
                    onClick={() => editVideo(video)}
                    className="btn-secondary"
                    style={{
                      padding: '8px 16px',
                      background: '#ff9800',
                      color: 'white',
                      border: 'none',
                      borderRadius: '8px',
                      cursor: 'pointer',
                      fontSize: '14px'
                    }}
                  >
                     
                  </button>
                  <button
                    onClick={() => deleteVideo(video.id)}
                    className="btn-delete"
                    style={{
                      padding: '8px 16px',
                      background: '#f44336',
                      color: 'white',
                      border: 'none',
                      borderRadius: '8px',
                      cursor: 'pointer',
                      fontSize: '14px'
                    }}
                  >
                     
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}
      </section>
    </div>
  );
}

