import React, { useEffect, useMemo, useRef, useState, useCallback } from 'react';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

function StatusTag({ status }) {
  const map = {
    pending:   { text: ' ',  color: '#8a6d3b', bg: '#fff3cd', border: '#ffe8a1', icon: '' },
    confirmed: { text: '', color: '#1b5e20', bg: '#e8f5e9', border: '#c8e6c9', icon: '' },
    cancelled: { text: '',     color: '#b71c1c', bg: '#ffebee', border: '#ffcdd2', icon: '' },
    completed: { text: '',    color: '#0d47a1', bg: '#e3f2fd', border: '#bbdefb', icon: ''  },
  };
  const s = map[status] || { text: status, color: '#37474f', bg: '#eceff1', border: '#cfd8dc', icon: '' };
  return (
    <span
      style={{
        display: 'inline-flex',
        alignItems: 'center',
        gap: 8,
        padding: '6px 12px',
        borderRadius: 9999,
        background: s.bg,
        color: s.color,
        border: `1px solid ${s.border}`,
        fontSize: 12,
        fontWeight: 700,
        minWidth: 128,
        justifyContent: 'center'
      }}
      title={s.text}
    >
      <span aria-hidden="true">{s.icon}</span>
      {s.text}
    </span>
  );
}

export default function AppointmentsAdmin() {
  const [items, setItems] = useState([]);
  const [statusFilter, setStatusFilter] = useState('pending');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const FILTER_OPTIONS = [
    { value: '', label: '' },
    { value: 'pending', label: ' ' },
    { value: 'confirmed', label: '' },
    { value: 'cancelled', label: '' },
  ];

  function FilterSelect({ value, onChange }) {
    const [open, setOpen] = useState(false);
    const ref = useRef(null);

    useEffect(() => {
      const onClick = (e) => {
        if (!ref.current) return;
        if (!ref.current.contains(e.target)) setOpen(false);
      };
      document.addEventListener('mousedown', onClick);
      return () => document.removeEventListener('mousedown', onClick);
    }, []);

    const currentLabel = FILTER_OPTIONS.find(o => o.value === value)?.label || '';

    return (
      <div ref={ref} className={`filter-select ${open ? 'open' : ''}`}>
        <button type="button" className="filter-select__control" onClick={() => setOpen(o => !o)}>
          <span>{currentLabel}</span>
          <span className="filter-select__arrow" aria-hidden="true"></span>
        </button>
        {open && (
          <div className="filter-select__menu" role="listbox" aria-activedescendant={`opt-${value || 'all'}`} tabIndex={0}>
            {FILTER_OPTIONS.map(o => (
              <div
                key={o.value || 'all'}
                id={`opt-${o.value || 'all'}`}
                role="option"
                aria-selected={o.value === value}
                className={`filter-select__option ${o.value === value ? 'is-selected' : ''}`}
                tabIndex={0}
                onClick={() => { onChange(o.value); setOpen(false); }}
                onKeyDown={(e) => { if (e.key === 'Enter' || e.key === ' ') { onChange(o.value); setOpen(false); } }}
              >
                {o.label}
              </div>
            ))}
          </div>
        )}
      </div>
    );
  }

  const load = useCallback(async () => {
    try {
      setLoading(true);
      setError('');
      const qs = new URLSearchParams();
      if (statusFilter) qs.set('status', statusFilter);
      const resp = await fetch(`${API_URL}/api/appointments?` + qs.toString());
      const json = await resp.json();
      if (!resp.ok || !json?.ok) throw new Error(json?.error || ' ');
      setItems(json.data || []);
    } catch (e) {
      console.error(e);
      setError(e.message);
    } finally {
      setLoading(false);
    }
  }, [statusFilter]);

  useEffect(() => { load(); }, [load]);

  const confirm = async (id) => {
    if (!window.confirm(' ?')) return;
    try {
      setLoading(true);
      const resp = await fetch(`${API_URL}/api/appointments/${id}/confirm`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({}),
      });
      const json = await resp.json();
      if (!resp.ok || !json?.ok) throw new Error(json?.error || '  ');
      await load();
      alert('  .    Telegram.');
    } catch (e) {
      alert(': ' + e.message);
    } finally {
      setLoading(false);
    }
  };

  const cancel = async (id, appointmentData = null) => {
    //       
    const cancelType = window.prompt(
      '  ?\n' +
      '1 - \n' +
      '2 - \n' +
      '  (1  2):'
    );
    
    if (!cancelType || (cancelType !== '1' && cancelType !== '2')) {
      return; //  
    }
    
    const cancelledBy = cancelType === '1' ? 'admin' : 'psychologist';
    const cancelledByLabel = cancelType === '1' ? '' : '';
    
    //  
    const cancelledByName = window.prompt(`  ${cancelledByLabel} ():`);
    
    //  
    const reason = window.prompt(' ? ()');
    
    if (!window.confirm(' ?')) return;
    
    try {
      setLoading(true);
      const resp = await fetch(`${API_URL}/api/appointments/${id}/cancel`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ 
          reason: reason || undefined,
          cancelledBy,
          cancelledByName: cancelledByName || undefined
        }),
      });
      const json = await resp.json();
      if (!resp.ok || !json?.ok) throw new Error(json?.error || '  ');
      await load(); //    
      alert('  .    Telegram   email.');
    } catch (e) {
      alert(': ' + e.message);
    } finally {
      setLoading(false);
    }
  };

  const grouped = useMemo(() => {
    const map = new Map();
    for (const a of items) {
      const key = a.appointment_date;
      if (!map.has(key)) map.set(key, []);
      map.get(key).push(a);
    }
    return Array.from(map.entries()).sort((a, b) => a[0].localeCompare(b[0]));
  }, [items]);

  return (
    <section className="admin-section">
      <div className="appointments-header" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 16 }}>
        <h2 className="section-title">  </h2>
        <div className="appointments-controls" style={{ display: 'flex', gap: 8 }}>
          <FilterSelect value={statusFilter} onChange={setStatusFilter} />
          <button className="btn btn-secondary" onClick={load} disabled={loading}></button>
        </div>
      </div>
      {error && <div className="message error" style={{ marginBottom: 12 }}>{error}</div>}
      {loading && <div></div>}
      {!loading && grouped.length === 0 && <div className="empty-state"> </div>}
      {!loading && grouped.length > 0 && (
        <div style={{ display: 'flex', flexDirection: 'column', gap: 16 }}>
          {grouped.map(([date, list]) => (
            <div key={date} className="appointment-card" style={{ padding: 16 }}>
              <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 8 }}>
                <h3 className="card-title" style={{ marginBottom: 0 }}>: {date}</h3>
                <span style={{ color: '#607d8b' }}>: {list.length}</span>
              </div>
              <div className="appointments-list">
                {list.map(a => (
                  <div key={a.id} className="appointment-row">
                    <div>
                      <div style={{ fontWeight: 600 }}>{(a.appointment_time || '').slice(0,5)}</div>
                      <div style={{ fontSize: 12, color: '#666' }}>{a.psychologists?.name_ru || ''}</div>
                    </div>
                    <div>
                      <div style={{ fontWeight: 600 }}>{a.client_name || ' '}</div>
                      <div style={{ fontSize: 12, color: '#666' }}>{a.client_phone || a.client_email || ''}</div>
                    </div>
                    <div>
                      <StatusTag status={a.status} />
                      {a.notes ? (<div style={{ fontSize: 12, color: '#888', marginTop: 4, whiteSpace: 'pre-wrap' }}>{a.notes}</div>) : null}
                    </div>
                    <div className="appointment-actions">
                      {a.status !== 'confirmed' && (
                        <button className="btn btn-primary btn-small" onClick={() => confirm(a.id)}></button>
                      )}
                      {a.status !== 'cancelled' && (
                        <button className="btn btn-secondary btn-small" onClick={() => cancel(a.id)}></button>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      )}
    </section>
  );
}


