import { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useLanguage } from '../../context/LanguageContext';
import Search from '../Search/Search';
import './Header.css';

const Header = () => {
  const { language, toggleLanguage, t } = useLanguage();
  const location = useLocation();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  const isActive = (path) => {
    return location.pathname === path ? 'active' : '';
  };

  const navLinks = [
    { path: '/', label: { ru: '', kz: ' ' } },
    { path: '/psychologists', label: { ru: '', kz: '' } },
    { path: '/alphabet', label: { ru: ' ', kz: ' ' } },
    { path: '/students', label: { ru: '', kz: '' } },
    { path: '/survey', label: { ru: '', kz: '' } },
    { path: '/news', label: { ru: '', kz: '' } },
    { path: '/podcasts', label: { ru: '', kz: '' } },
    { path: '/stories', label: { ru: ' ', kz: ' ' } },
    { path: '/contacts', label: { ru: '', kz: '' } },
  ];

  const handleMobileMenuToggle = () => {
    setMobileMenuOpen(!mobileMenuOpen);
  };

  const closeMobileMenu = () => {
    setMobileMenuOpen(false);
  };

  return (
    <header className={`header ${language === 'kz' ? 'lang-kz' : ''}`}>
      <div className="container">
        <div className="header-content">
          <div className="logo">
            <Link to="/">
              <h1>Sezim.abu</h1>
            </Link>
          </div>
          <nav className="nav">
            <ul className={`nav-list ${mobileMenuOpen ? 'active' : ''}`}>
              {navLinks.map((link) => (
                <li key={link.path}>
                  <Link
                    to={link.path}
                    className={`nav-link ${isActive(link.path)}`}
                    onClick={closeMobileMenu}
                  >
                    {t(link.label)}
                  </Link>
                </li>
              ))}
            </ul>
            <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
              <Search />
              <div className="language-switcher">
                <button
                  className={`lang-btn ${language === 'ru' ? 'active' : ''}`}
                  onClick={() => toggleLanguage('ru')}
                >
                  RU
                </button>
                <button
                  className={`lang-btn ${language === 'kz' ? 'active' : ''}`}
                  onClick={() => toggleLanguage('kz')}
                >
                  KZ
                </button>
              </div>
            </div>
            <button
              className={`mobile-menu-toggle ${mobileMenuOpen ? 'active' : ''}`}
              onClick={handleMobileMenuToggle}
              aria-label={t({ ru: '', kz: '' })}
            >
              <span></span>
              <span></span>
              <span></span>
            </button>
          </nav>
        </div>
      </div>
    </header>
  );
};

export default Header;

